#newsbox {
    overflow-y: scroll;
    font-size:12px;
    text-align: left;
    border:0.8px solid #25348c;

}
.articles{
	padding-left: 2%;
  padding-bottom: 2%;
  border-bottom: 1px solid #aeafae;
  color:#dddddd;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 4px;
  border-left:1px solid #25348c;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #25348c ;
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}


.articletext{
	 color:black;
text-decoration: none;
    margin-left: 1%;
}


#wodtitle{
	padding-left:1%;
	display: inline-block;
	font-size: 25px;
	margin:1% 2% 1% 2%;
	color:#3399ff;
	font-family: 'Verdana', cursive;
}



#redbutton:hover{
	cursor:pointer;

}
#redbutton{
	display:inline-block;

	margin-left: 3%;
	margin-right: 3%;

}


.nav{
	background-color: #aeafae;
	border-bottom: 2px solid #25348c;
}

#scrapedarticles{
	        margin-top: -1%;

}



